export default {
  props: {
    tagList: {
      type: Array,
      require: true
    }
  },
  name: 'article-tags',
  data: function data() {
    return {};
  },
  created: function created() {
    console.log(this.tagList);
    this.init();
  },
  methods: {
    init: function init() {
      this.assert();
    },
    assert: function assert() {
      if (!this.tagList) {
        throw new Error('必须传入具体的tag标签数组，数组格式[{tagName:"标签名",tagId:"标签id可以不传"}]');
      }
    }
  }
};